.game {
  width: 600px;
  height: 200px;
  border-bottom: 1px solid #000;
  margin: auto;
}
#dino {
  width: 50px;
  height: 50px;
  background-image: url(../img/rex.png);
  background-size: 50px 50px;
  position: relative;
  top: 150px;
}
#cactus {
  width: 20px;
  height: 40px;
  background-image: url(../img/cactus.png);
  background-size: 20px 40px;
  position: relative;
  top: 110px;
  left: 580px;
  animation: cactusMow 3s infinite linear;
}
@keyframes cactusMow  {
  0% {
    left: 580px;
  }
  100% {
    left: -20px;
  }
}
.jump {
  animation: jump 0.5s linear;
}
@keyframes jump {
  0% {
    top: 100px;
  }
  30% {
    top: 60px;
  }
  50% {
    top: 30px;
  }
  80% {
    top: 60px;
  }
  100% {
    top: 100px;
  }
}

